<template lang="pug">
  button.go-home(@click="goHome()") 回 E2E
</template>

<script>
import { getAlbumsInfo } from "../aws.js";

export default {
  name: "GoHome",
  methods: {
    goHome() {
      history.pushState(
        null,
        document.title,
        location.pathname + location.search
      );
      history.replaceState(null, document.title, location.pathname);

      getAlbumsInfo();
    }
  }
};
</script>

