import AWS from 'aws-sdk';
import store from './store';

const bucketRegion = 'ap-northeast-1';
const IdentityPoolId = 'ap-northeast-1:f35bb3c6-9047-4bc1-8834-55bf92201ca5';

const albumBucketName = 'dylan0916bucket02';

AWS.config.region = bucketRegion; // 區域
AWS.config.credentials = new AWS.CognitoIdentityCredentials({ IdentityPoolId });

const s3 = new AWS.S3({
  apiVersion: '2006-03-01',
  params: { Bucket: albumBucketName },
});

const preSwitchAlbums = albumName => {
  store.commit('setCurrentAlbum', albumName);
  store.commit('setGoHomeBtnStatus');
  store.commit('setAlbums', []);
  store.commit('setPhotos', []);
};

const getAlbums = ({ data, albumNameKey }) => {
  return data.CommonPrefixes.map(commonPrefix => {
    const prefix = commonPrefix.Prefix;
    const pattern = new RegExp(`(${albumNameKey}|\/)`, 'g');
    const path = encodeURIComponent(prefix.replace(/\/$/, ''));
    const name = decodeURIComponent(prefix.replace(pattern, ''));

    return { path, name };
  });
};

const getPhotos = ({ data, bucketUrl, albumNameKey }) => {
  return data.Contents.filter(photo => photo.Size > 0).map(photo => {
    const photoKey = photo.Key;
    const url = bucketUrl + encodeURIComponent(photoKey);

    return { url, name: photoKey.replace(albumNameKey, '') };
  });
};

export const getAlbumsInfo = albumName => {
  const formatAlbumName = decodeURIComponent(albumName || 'E2E');
  const albumNameKey = `${formatAlbumName}/`;
  preSwitchAlbums(formatAlbumName);

  s3.listObjects({ Prefix: albumNameKey, Delimiter: '/' }, function(err, data) {
    if (err) {
      return console.log('There was an error viewing your album:', err.message);
    }

    const { href } = this.request.httpRequest.endpoint;
    const bucketUrl = `${href + albumBucketName}/`;

    store.commit('setAlbums', getAlbums({ data, albumNameKey }));
    store.commit('setPhotos', getPhotos({ data, bucketUrl, albumNameKey }));
  });
};
