import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentAlbum: 'E2E',
    showGoHomeBtn: false,
    albums: [],
    photos: [],
  },
  mutations: {
    setCurrentAlbum(state, val) {
      state.currentAlbum = val;
    },
    setGoHomeBtnStatus(state) {
      const urlParams = new URLSearchParams(window.location.search);
      const path = urlParams.get('path');

      state.showGoHomeBtn = !!path;
    },
    setAlbums(state, albums) {
      state.albums = albums;
    },
    setPhotos(state, photos) {
      state.photos = photos;
    },
  },
  actions: {},
});
