<template lang="pug">
  #app
    h1 Album: {{ currentAlbum }}
    GoHome(v-show="showGoHomeBtn")
    AlbumList
    AlbumContent
</template>

<script>
import { mapState } from "vuex";
import { getAlbumsInfo } from "./aws.js";

import GoHome from "./components/GoHome";
import AlbumList from "./components/AlbumList";
import AlbumContent from "./components/AlbumContent";

const updateAlbums = () => {
  const urlParams = new URLSearchParams(window.location.search);
  const path = urlParams.get("path");

  getAlbumsInfo(path);
};

export default {
  name: "app",
  components: { GoHome, AlbumList, AlbumContent },
  mounted() {
    window.onpopstate = () => updateAlbums();
    updateAlbums();
  },
  computed: {
    ...mapState(["currentAlbum", "showGoHomeBtn"])
  }
};
</script>

<style lang="scss">
html,
body {
  margin: 0;
}

#app {
  margin-left: 10px;
}
</style>
