<template lang="pug">
  div(v-if="albums.length")
    h3 Switch Folders:
    ul.albums-container
      li(v-for="album in albums", :key="album.name")
        span.album-link(@click="getAlbumsInfo(album.path)") {{ album.name }}
</template>

<script>
import { getAlbumsInfo } from "../aws.js";

import { mapState } from "vuex";

export default {
  name: "AlbumList",
  computed: {
    ...mapState(["albums"])
  },
  methods: {
    getAlbumsInfo(path) {
      history.pushState(
        null,
        document.title,
        location.pathname + location.search
      );

      history.replaceState(
        null,
        document.title,
        `${location.pathname}?path=${path}`
      );

      getAlbumsInfo(path);
    }
  }
};
</script>

