<template lang="pug">
  .album-wrapper
    .img-container(
      v-for="(photo, idx) in photos", :key="photo.name",
      @click="handleLightbox(idx)"
    )
      img(:src="photo.url")
      p {{ photo.name }}

    Lightbox(
      :visible="lightboxCofig.open",
      :imgs="lightboxImg",
      :index="lightboxCofig.index",
      @hide="lightboxCofig.open = false"
    )
</template>

<script>
import { mapState } from "vuex";
import Lightbox from "vue-easy-lightbox";

export default {
  name: "AlbumContent",
  components: { Lightbox },
  data() {
    return {
      lightboxCofig: {
        open: false,
        index: 0
      }
    };
  },
  computed: {
    ...mapState(["photos"]),
    lightboxImg() {
      return this.photos.map(photo => photo.url);
    }
  },
  methods: {
    handleLightbox(idx) {
      this.$set(this.lightboxCofig, "index", idx);
      this.$set(this.lightboxCofig, "open", true);
    }
  }
};
</script>

<style lang="scss" scoped>
.album-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-top: 15px;
}

.img-container {
  margin-right: 10px;
  margin-bottom: 15px;
  cursor: pointer;

  p {
    margin-top: 5px;
    margin-bottom: 0;
  }

  img {
    width: 135px;
    border: solid 1px #b1a2a2;
  }
}
</style>